<template>
	<div class="singer" ref="singer">
		<listview @select="selectSinger" :data="singer" ref="list"></listview>
		<transition name="fade">
			<router-view></router-view>
		</transition>
	</div>
</template>

<script>
import axios from 'axios'
import Singer from 'common/js/singer'
import Listview from 'base/listview/listview'
import {mapMutations} from 'vuex'
import {playlistMixin} from 'common/js/mixin'
import {getSingerList} from 'api/singer'
import {ERR_OK} from 'api/config'

const HOT_NAME ='热门';
const HOT_SINGER_LEN = 10; //十条热门
export default {
	mixins: [playlistMixin],
	data() {
		return {
			singer: [],
			//newSinger: []

		};
	},
	watch: {
		/*singer() {
			this._normalizeSinger(this.singer);
		}*/ //直接axios res返回时 运行 这段  = this.singer
		
	},
	created() {
		this._mockSinger();
		//setTimeout(() => {
			//this._normalizeSinger();
		//},800)
		
	},
	methods: {
		handlePlaylist(playlist) {
			const bottom = playlist.length > 0 ? '60px' : '';
			this.$refs.singer.style.bottom = bottom;
			this.$refs.list.refresh();
		},	
		selectSinger(singer) {
			//console.log(singer)
			this.$router.push({
				path: `/singer/${singer.id}`
			})
			this.setSinger(singer)
		},
		_mockSinger() {
			/*axios.get('http://rapapi.org/mockjsdata/26432/singerlist')
			.then(res => {
				if (res.status == 200) {
					this.singer = this._normalizeSinger(res.data.data.list);
					console.log(this.singer);
				}
			})*/
			getSingerList().then(res => {
				if (res.code === ERR_OK) {
					this.singer = this._normalizeSinger(res.data.list)
				}
			})
		},
		_normalizeSinger(list) {
			
			let map = {
				hot: {
					title: HOT_NAME,
					items: []
				}
			};
			list.forEach((item, index) => {
				if (index < HOT_SINGER_LEN) {
					map.hot.items.push(new Singer({
						id: item.Fsinger_mid,
						name: item.Fsinger_name
					}))
				}
				const key = item.Findex;
				if (!map[key]) {
					map[key] = {
						title: key,
						items: []
					}
				}
				map[key].items.push(new Singer({
					id: item.Fsinger_mid,
					name: item.Fsinger_name
				}))

			});

			let hot = [];
			let ret = [];
			for (let key in map) {
				let val = map[key];
				if (val.title.match(/[a-zA-Z]/ig)) {
					ret.push(val);
				} else if (val.title === HOT_NAME) {
					hot.push(val);
				}
			}

			ret.sort((a, b) => {
				return a.title.charCodeAt(0) - b.title.charCodeAt(0);
			})
			let newArr = hot.concat(ret);
			///-----
			newArr[0].items.unshift(new Singer({
				id: '000L9viB2qAltM',
				name: 'GNZ48'
			}))
			newArr[0].items.unshift(new Singer({
				id: '003BLxYj1Smxh4',
				name: 'SNH48'
			}))
			//----
			
			return newArr
			
		},
		...mapMutations({
			setSinger: 'SET_SINGER'
		})
	},
	components: {
		Listview
	}
}
</script>

<style lang="stylus" scoped>
	.singer
		position fixed
		top 88px
		left 0
		bottom 0
		width 100%
		
.fade-enter-active, .fade-leave-active
	transition all .5s
.fade-enter,.fade-leave-to
	transform translate3d(100%,0,0)
		
</style>


