<template>
	<div class="progress-bar" ref="progressBar" @click="progressClick">
		<div class="bar-inner" >
			<div class="progress" ref="progress"></div>
			<div 
				class="progress-btn-wrapper"
				ref="progressBtn"
				@touchstart.stop.prevent="progressTouchstart"
				@touchmove.stop.prevent="progressTouchmove"
				@touchend.stop.prevent="progressTouchend"
			>
				<div class="progress-btn"></div>
			</div>
		</div>
	</div>
</template>

<script>
import {prefixStyle} from 'common/js/dom'
const progressBtnWidth = 14;
const transform = prefixStyle('transform');
export default {
	props: {
		percent: {
			type: Number,
			default :0
		}
	},
	created() {
		this.touch = {};
		this.pageX = 0;
	},
	watch: {
		percent(newPercent) {
			if (newPercent >= 0 && !this.touch.initiated) {
				const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
				const offsetWidth = Math.floor(newPercent * barWidth) + 1;
				//console.log(newPercent*161)
				this._offset(offsetWidth);
			}
		}
	},
	methods: {
		_offset(offsetWidth) {
			this.$refs.progress.style.width = `${offsetWidth}px`;
			this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px,0,0)`
		},
		progressTouchstart(e) {
			
				
			this.touch.initiated = true;
			this.touch.startX = e.touches[0].pageX;		
			this.touch.left = this.$refs.progress.clientWidth;
			
			


		},
		progressTouchmove(e) {
			
			if (!this.touch.initiated) {
				return;
			}
			const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
			let delta = e.touches[0].pageX - this.touch.startX;
			let offsetWidth = Math.min(barWidth, Math.max(0, this.touch.left + delta));
			this._offset(offsetWidth);
			


		},
		progressTouchend() {
			this.touch.initiated = false;
			this.setPercent();
		},
		progressClick(e) {	
			//this.$refs.progressBar.getBoundingClientRect().left
			let diff = e.offsetX - progressBtnWidth / 2;
			if (e.offsetX < 7) {
				diff = 0;
			}
			this._offset(diff);
			this.setPercent();		
		},
		setPercent() {
			const left = this.$refs.progress.clientWidth;
			const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
			let precent = left / barWidth ;
			//console.log(newPrecent)
			this.$emit('changePrecent', precent);
		},

	}
	
}
</script>

<style lang="stylus">
	@import '~common/stylus/variable'
	
	.progress-bar
		height 30px
		.bar-inner
			position relative
			top 13px
			height 4px
			background rgba(0,0,0,.3)
			.progress
				position absolute
				height 100%
				background $color-theme
			.progress-btn-wrapper
				position absolute
				left -8px
				top -13px
				width 30px
				height 30px
				.progress-btn
					position relative
					top 7px
					left 7px
					box-sizing border-box
					width 16px
					height 16px
					border 3px solid $color-text
					border-radius 50%
					background $color-theme
</style>


