<template>
	<div class="song-list">
		<ul>
			<li @click="selectItem(song, index)" v-for="(song, index) in songs" class="item">
				<div class="rankIcon" v-if="rank">
					<span :class="getRankCls(index)">{{getRankTxt(index)}}</span>
				</div>
				<div class="content">
					<h2 class="name">{{song.name}}</h2>
					<p class="desc">{{getDesc(song)}}</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	props: {
		songs: {
			type: Array,
			default: []
		},
		rank: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		selectItem(item, index) {
			this.$emit('select', item, index);
		},
		getDesc(song) {
			return `${song.singer} - ${song.album}`
		},
		getRankCls(index) {
			if (index <= 2) {
				return `icon icon${index}`
			} else {
				return 'text'
			}
		},
		getRankTxt(index) {
			return index + 1
		}
	}
}
</script>

<style lang="stylus" scoped>
	@import '~common/stylus/variable'
	@import '~common/stylus/mixin'
	
	.song-list
		.item
			display flex
			//padding-left 20px//
			align-items center
			box-sizing border-box
			height 64px
			font-size $font-size-medium
			.rankIcon
				flex 0 0 25px
				width 25px
				margin-right 30px
				text-align center
				.icon
					display inline-block
					width 25px
					height 24px
					background-size 25px 24px
					&.icon0
						background-image url('img1.png')
					&.icon1
						background-image url('img2.png')
					&.icon2
						background-image url('img3.png')
				.text
					color $color-theme
					font-size $font-size-large
			.content
				flex 1
				line-height 20px
				overflow hidden
				.name
					no-wrap()
					color $color-text
				.desc
					no-wrap()
					margin-top 4px
					color $color-text-d
</style>


