//constructor 构造器  理解为 Singer.id =id   Singer.name = name
//前面push了 new Singer 这里又构造了Singer类 类里面的id name avatar对应
export default class Singer {
	constructor({id,name}) {
		this.id = id
		this.name = name
		this.avatar = `https://y.gtimg.cn/music/photo_new/T001R150x150M000${this.id}.jpg?max_age=2592000`
	}
}




