<template>
	<transition name="slide">
		<music-list :rank="rank" :title="title" :bgImage="bgImage" :songs="songs"></music-list>
	</transition>
</template>

<script>
import MusicList from '@/components/music-list/music-list'
import {mapGetters} from 'vuex'
import {getRankList} from 'api/getrank'
import {ERR_OK} from 'api/config'
import {createSong} from 'common/js/song'
export default {
	data() {
		return {
			songs: [],
			rank :true
		}
	},	
	created() {
		this._getRankList();
	},
	methods: {
		_getRankList() {
			if (!this.topList.id) {
				this.$router.push('/rank')
				return;
			}
			getRankList(this.topList.id).then(res => {
				if (res.code === ERR_OK) {
					this.songs = this._normalizeRank(res.songlist)
					//console.log(this.songs)
				}
			})
		},
		_normalizeRank(list) {
			let arr = []
			
			list.forEach(item => {
				const musicData = item.data;
				if (musicData.songid && musicData.albumid) {
					arr.push(createSong(musicData))
				}
				
			})
				
			
			return arr;
		}
	},

	computed: {
		title() {
			return this.topList.topTitle
		},
		bgImage() {
			if (this.songs.length) {
				return this.songs[0].image;

			}
			return '';
			
		},
		...mapGetters([
			'topList'
		])
	},
	components: {
		MusicList
	}
	
}
</script>

<style scoped lang="stylus">
	.slide-enter-active, .slide-leave-active
		transition all .3s ease
	.slide-enter, .slide-leave-to
		transform translate3d(100%, 0, 0)
</style>


