<template>
	<div class="slider" ref="slider">
		<div class="slider-group" ref="sliderGrop">
			<slot>
				
			</slot>
		</div>
		<div class="dots">
			<span class="dot" v-for="(item, index) in dots" :class="{active: currentPageIndex === index}"></span>
		</div>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	import {addClass} from 'common/js/dom'
	export default {
		data() {
			return {
				dots: [],//按钮数量 循环出来
				currentPageIndex: 0
			};
		},
		props: {
			loop: {
				type: Boolean,
				default: true
			},
			autoPlay: {
				type: Boolean,
				default: true
			},
			interval: {
				type: Number,
				default: 4000
			}
		},
		mounted() {
			//setTimeout(() => {	
			this.$nextTick(() => {
				this._setSliderWidth();
				this._initDots();
				this._initSlider();

				if (this.autoPlay) {
					this._play();
				}

			})	

			//}, 20)
			
			window.addEventListener('resize', () => {
				
					if (!this.slider) {
						return;
					}
					if (!this.$refs.sliderGrop) {
						return;
					}
					this._setSliderWidth(true);
					this.slider.refresh();
					this._play(true);
				
			})
		},
		activated() { //加了keep-alive 可以调用 created无法调用
			this._setSliderWidth();
			this.slider.refresh();

			if (this.autoPlay) {
				//clearTimeout(this.timer);
				this._play();
			}
		},
		deactivated() { // keep-alive 组件停用时调用。
			//console.log('deactivated')
			
			clearTimeout(this.timer);
		},
		beforeDestroy() { //实例销毁之前调用。在这一步，实例仍然完全可用。
			//console.log('beforeDestroy')
			clearTimeout(this.timer);
		},
		methods: {
			_setSliderWidth(isResize) {

				this.children = this.$refs.sliderGrop.children;
				//console.log(this.children.length)
				let width = 0;
				let sliderWidth = this.$refs.slider.clientWidth;
				for (let i = 0; i < this.children.length;i++) {
					let child = this.children[i];
					addClass(child, 'slider-item');	
					child.style.width = sliderWidth + 'px';							
					width += sliderWidth;
				}
				if (this.loop && !isResize) {  //无缝滚动多加两个dom
					width += 2 * sliderWidth;
				}
				
				this.$refs.sliderGrop.style.width = width + 'px'
			
				
				//console.log(width);
			},
			_initDots() {
				this.dots = new Array(this.children.length);
				//console.log(this.children)
			},
			_initSlider() {

				this.slider = new BScroll(this.$refs.slider, {
					probeType: 3,	
					scrollX: true,
					scrollY: false,
					momentum: false,
					snap: {loop: this.loop},
					//snapLoop: true,
					snapThreshold: 0.3,
					snapSpeed: 400,
					click: true
				});

				this.slider.on('beforeScrollStart', () => {//滚动前 鼠标按下
					//console.log('star')
					if (this.autoPlay) {
						clearTimeout(this.timer)
					}
				})

				this.slider.on('scrollStart',() => { //滚动开始时 鼠标按下并拖动
					//console.log('scrollStart')
					if (this.autoPlay) {
						clearTimeout(this.timer);
					}
				});

				/*this.slider.on('scroll', () => {
					clearTimeout(this.timer);
				})*/

				this.slider.on('scrollEnd',() => {
					//alert('a') 滚动完毕打印一个a
					//console.log('a')
					let pageIndex = this.slider.getCurrentPage().pageX;
					if (this.loop) {
						pageIndex -= 1;
					}
					//console.log(pageIndex)
					this.currentPageIndex = pageIndex;

					if (this.autoPlay) {
						clearTimeout(this.timer);
						this._play();
					}
				});

				

			},
			_play(status) {
				if(status) {
					clearTimeout(this.timer);
				}
				let pageIndex = this.currentPageIndex + 1;
				if (this.loop) {
					pageIndex += 1;
				}

				this.timer = setTimeout(() => {
				this.slider.goToPage(pageIndex, 0, 400) //0横向 400轮播图切换的动画时间
				}, this.interval)
				
				
			}
		},
		destroyed() {
			//console.log('destroyed')
			clearTimeout(this.timer)
		}
	}
</script>

<style scoped lang="stylus">
	@import '~common/stylus/variable'
	.slider
		min-height 1px		
		.slider-group
			position relative
			overflow hidden
			white-space nowrap
			.slider-item
				float left
				box-sizing border-box
				overflow hidden
				text-align center
				a
					display block
					img
						display block
						width 100%				
		.dots
			position absolute
			right 0
			left 0
			bottom 12px
			text-align center
			font-size 0						
			.dot
				display inline-block
				margin 0 4px
				width 8px
				height 8px
				border-radius 50%
				background $color-text-l
				&.active
					width 20px
					border-radius 5px
					background $color-text-ll
</style>


