<template>
	<scroll class="listview" :data="data" ref="listview" :probeType="3" :listenScroll="true" @scroll="scroll">
		<ul ref="listGroupParent">
			<li v-for="group in data" class="list-group" ref="listGroup">
				<h2 class="list-group-title" >{{group.title}}</h2>
				<ul>
					<li @click="selectItem(item)" v-for="item in group.items" class="list-group-item" >
						<img class="avatar" v-lazy="item.avatar" width="50" height="50">
						<span class="name">{{item.name}}</span>
					</li>
				</ul>
			</li>
		</ul>
		<div class="list-shortcut" @touchstart="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove">
			<ul>
				<li v-for="(item, index) in shortcutList" class="item" :data-index="index" :class="{current: index === currentIndex}">
					{{item}}
				</li>
			</ul>
		</div>
		<div class="list-fixed" ref="fixedList">
			<h1 class="fixed-title">
				{{fixedTitle}}
			</h1>
		</div>
		<loading v-show="!data.length"></loading>
	</scroll>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import loading from 'base/loading/loading'
import {getData} from 'common/js/dom'


const ANCHOR_HEIGHT = 18;
const HOT_TITLE = '热门';
const TITLE_HEIGHT = 28;
export default {
	data() {
		return {
			scrollY:-1,
			currentIndex: 0,
			diff: -1
		};
		
	},
	created() {
		this.touch = {};//不需要监听变化 只为了获取数据
		this.listHeight = []
		
	},
	watch: {

		data() {
			this.$nextTick(() => {
				this._getTotalHeight();
			})
			
		},
		scrollY() {			
			this.$nextTick(() => {
				this._getCurrentIndex();
			})					
		},
		diff(newVal) {
			let fixedTop = (newVal > 0 && newVal < TITLE_HEIGHT) ? newVal - TITLE_HEIGHT : 0;
			if (this.fixedTop === fixedTop) {
				return;
			}
			this.fixedTop = fixedTop;
			this.$refs.fixedList.style.transform = `translate3d(0,${fixedTop}px,0)`
		}
		
		
	},
	props: {//props,data,computed可以监听变化
		data: {
			type: Array,
			default: []
		}
	},
	computed: {
		shortcutList() {
			return this.data.map((group) => {
				return group.title.substr(0, 1);
			})
		},
		fixedTitle() {
			return this.data[this.currentIndex] ? this.data[this.currentIndex].title : '';
			//return this.scrollY;
		},
		
	},
	methods: {
		refresh() {
			this.$refs.listview.refresh();
		},
		selectItem(item) {
			this.$emit('select', item);
		},
		onShortcutTouchStart(e) {
			
			let anchorIndex = parseInt(getData(e.target, 'index'));
			let firstTouch = e.touches[0].pageY;
			this.touch.y1 = firstTouch;
			this.touch.anchorIndex = anchorIndex;//一开始点的是几个索引
			this.toScroll(anchorIndex);
			//this.currentIndex = anchorIndex;//getHighlightIndex高亮索引

		},
		onShortcutTouchMove(e) {
			
			let firstTouch = e.touches[0].pageY;
			this.touch.y2 = firstTouch;
			
			let delta = Math.floor((this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT) 
			//y轴偏移  |0 = Math.floor
			let anchorIndex = this.touch.anchorIndex + delta;
			this.toScroll(anchorIndex);
			/*if (anchorIndex < 0 || anchorIndex > this.shortcutList.length - 1) {
				return;
			}*///getHighlightIndex 判断
			//this.currentIndex = anchorIndex;//getHighlightIndex高亮索引
			//console.log(anchorIndex)


		},
		toScroll(index){

			if (isNaN(index) || index < 0 || index > this.shortcutList.length - 1) {
				return ;
			} else {
				this.scrollY = this.listHeight[index];
				this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0);
			}
			

		},
		scroll(pos) {
			this.scrollY = Math.abs(Math.floor(pos.y));
			//console.log(this.scrollY)
		},
		_getTotalHeight() {
			this.listHeight = [];
			let item = this.$refs.listGroup;
			let height = 0;
			this.listHeight.push(height);
			for (let i = 0; i < item.length; i++) {
				let child = item[i];
				height += child.clientHeight;
				this.listHeight.push(height);
			}
			//console.log(this.listHeight)

		},
		_getCurrentIndex() {
			const arr = this.listHeight;
			if (this.scrollY > 0) {
				this.currentIndex = 0;
				
				//return;
				
			}
			for (let i = 0; i < arr.length; i++) {
				
				let prev = arr[i];
				let next = arr[i + 1];
				if (this.scrollY >= prev && this.scrollY < next) {
					this.currentIndex = i;
					this.diff = next - this.scrollY;
					return;
				}
			}
			

	
		}
		
		
	},
	components: {
		Scroll,
		loading
	}
}
</script>

<style lang="stylus">
	@import '~common/stylus/variable'
	.listview
		position relative
		width 100%
		height 100%
		overflow hidden
		background $color-background
		.list-group
			padding-bottom 30px
			.list-group-title
				height 30px
				line-height 30px
				padding-left 20px
				font-size $font-size-small
				color $color-text-l
				background $color-highlight-background
			.list-group-item
				display flex
				align-items center
				padding 20px 0 0 30px
				.avatar
					border-radius 50%
				.name
					margin-left 20px
					color $color-text-l
					font-size $font-size-medium
		.list-shortcut
			position absolute
			z-index 30
			top 50%
			right 0
			transform translateY(-50%)//?
			width 20px
			padding 20px 0
			border-radius 10px
			text-align center
			background $color-background-d
			font-family Helvetica
			.item
				padding 3px
				line-height 1
				color $color-text-l
				font-size $font-size-small
				&.current
					color $color-theme
		.list-fixed
			position absolute
			top -1px
			left 0
			width 100%
			.fixed-title
				height 30px
				line-height 30px
				padding-left 20px
				font-size $font-size-small
				color $color-text-l
				background $color-highlight-background
		.loading
			position absolute
			top 45px
</style>


