<template>
	<transition name="slide">
		 <musiclist  :title="title" :bg-image="bgImage" :songs="songs"></musiclist> 
	</transition>
</template>

<script>
import musiclist from '@/components/music-list/music-list'
import {getRecomSonglist} from 'api/recommend'
import {mapGetters} from 'vuex'
import {ERR_OK} from 'api/config'
import {createSong} from 'common/js/song'

export default {
	data() {
		return {
			songs: []
		}
	},
	computed: {
		title() {
			return this.disc.title
		},
		bgImage() {
			return this.disc.cover
		},
		...mapGetters([
			'disc'
		])
	},
	created() {
		
		
		if (!this.disc.content_id) {
			this.$router.push('/recommend')
			return;
		}
		getRecomSonglist(this.disc.content_id).then(res => {
			if (res.code === ERR_OK) {
				this.songs = this._normallizeList(res.cdlist[0].songlist)
				//console.log(this.songs)
			}
		})
	},
	methods: {
		_normallizeList(list) {
			let arr = [];
			list.forEach(item => {
				if (item.songid && item.albumid) {
					arr.push(createSong(item))
				}
				
			})
			return arr
		},
		
	},
	components: {
		musiclist
	}
}
</script>

<style lang="stylus" scoped>
	.slide-enter-active, .slide-leave-active
		transition all .3s
	.slide-enter, .slide-leave-to
		transform translate3d(100%,0,0)
</style>


