<template>
	<div ref="wrapper">
		<slot>
			
		</slot>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
	props: {
		click: {
			type: Boolean,
			default: true
		},
		probeType: {
			type: Number,
			default: 1
		},
		data: {
			type: Array,
			default: null
		},
		listenScroll: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			
		};
	},
	watch: {
		data() {
			this.$nextTick(() => {
				this.refresh();
			})
			
		}
	},
	mounted() {
		this.$nextTick(() => {
			this._initScroll();
		})
		
	},
	activated() {
		this.refresh();
	},
	methods: {
		_initScroll() {
			if(!this.$refs.wrapper) {
				return;
			}
			if (!this.scroll) {
				this.scroll = new BScroll(this.$refs.wrapper, {
					probeType: this.probeType,
					click: this.click
				})
			} else {
				this.refresh();
			}

			if (this.listenScroll) {
				let me = this;
				this.scroll.on('scroll', (pos) => {
					me.$emit('scroll', pos)
				})
			}
			
		},
		enable() {
			this.scroll && this.scroll.enable();
		},
		disable() {
			this.scroll && this.scroll.disable();
		},
		refresh() {
			this.scroll && this.scroll.refresh();
		},
		scrollTo() {
			this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
		},
		scrollToElement() {
			this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
			//因为要传参数 所以用apply（请求） argument 是传进来的两个参数，0代表动画
		}

	}
}
</script>

<style lang="stylus">

</style>


