<template>
	<div class="recommend" ref="recommend">
		<scroll class="recommend-content" :data="descList" ref="scroll">
			<!-- 如果有再加载dom mounted就挂上了 -->
			<div>
				<div v-if="recommends.length" class="slider-wrapper">
					<slider>
						<div v-for="item in recommends">
							<a :href="item.linkUrl">
								<img @load="load" :src="item.picUrl" alt="">
							</a>
						</div>
					</slider>
				</div>
				<div class="recommend-list">
					<h1 class="list-title">热门歌单推荐</h1>
					<ul>
						<li @click="selectItem(item)"  v-for="item in descList" class="item">
							<div class="icon">
								<img width="60" height="60" v-lazy="item.cover" alt="">
							</div>
							<div class="text">
								<h2 class="name" v-html="item.username"></h2>
								<p class="desc" v-html="item.title"></p>
							</div>
						</li>
					</ul>
				</div>
			</div>
			<loading v-show="!descList.length"></loading>
		</scroll>
		<router-view></router-view>	
	</div>
</template>

<script>
	import jsonp from 'jsonp'
	import axios from 'axios'
	
	import {getRecommend} from 'api/recommend'
	import {getSongList} from 'api/recommend'
	import {ERR_OK} from 'api/config'
	import scroll from 'base/scroll/scroll'
	import slider from 'base/slider/slider'
	import loading from 'base/loading/loading'
	import {playlistMixin} from 'common/js/mixin'
	import {mapMutations} from 'vuex'
	
	export default {
		mixins: [playlistMixin],
		data() {
			return {
				recommends: [],
				descList:[]
				
				
			}
		},
		created() {
			setTimeout(() => {
				this._getSongList();
				this._getRecommemd();
			},20)
		},
		methods: {
			_getRecommemd() {
				getRecommend().then((res) => {
					if (res.code === ERR_OK) {
						this.recommends = res.data.slider;
						//console.log(this.recommends)
					}
				})
			},
			selectItem(item) {
				this.$router.push({
					path: `/recommend/${item['content_id']}`
				})
				this.setDisc(item)

			},	
			handlePlaylist(playlist) {
				const bottom = playlist.length > 0 ? '60px' : '';
				this.$refs.recommend.style.bottom = bottom;
				this.$refs.scroll.refresh();
			},
			/*mockRecommend() {
				axios.get('http://rapapi.org/mockjsdata/26432/music/music')
				.then(res => {
					if (res.status == 200) {
						this.recommends = res.data.data.slider;
						//console.log(res.data.data)
					}	
				})
				.catch(err => {
					console.log(err);
				})
			},*/
			_getSongList() {
				getSongList().then(res => {
					if (res.code === ERR_OK) {
						this.descList = res.recomPlaylist.data.v_hot
						//console.log(this.descList)
					}
				})
			},
			load() {
				if(!this.checkLoad) {
					this.$refs.scroll.refresh();
					this.checkLoad = true;

				}
				
			},
			...mapMutations({
				setDisc: 'SET_DISC'
			})
		
			

		},
		components: {
			slider,
			scroll,
			loading
		}
	}
</script>

<style lang="stylus">
	@import '~common/stylus/variable'
	
	.recommend
		position fixed
		width 100%
		top 88px
		bottom 0
		.recommend-content
			height 100%
			overflow hidden //temp
			.slider-wrapper
				position relative
				width 100%
				overflow hidden
			.recommend-list
				.list-title
					height 65px
					line-height 65px
					text-align center
					font-size $font-size-medium
					color $color-theme
				.item
					display flex
					box-sizing border-box//
					align-items center
					padding 0 20px 20px 20px
					.icon
						flex 0 0 60px
						width 60px
						padding-right 20px	
						img
							display block	
					.text	
						//display flex
						//flex-direction column	
						flex 1
						line-height 20px 
						overflow hidden
						font-size $font-size-medium
						.name
							margin-bottom 4px/*10*/
							color $color-text
						.desc
							color $color-text-d
							
				
</style>


