<template>
	<musiclist :songs="songs" :title="title" :bg-image="bgImage"></musiclist>
</template>

<script>
import {mapGetters} from 'vuex'
import axios from 'axios'
import jsonp from 'jsonp'
import {getSingerDetail} from 'api/singer'
import {ERR_OK} from 'api/config'
import {createSong} from 'common/js/song'
import musiclist from '@/components/music-list/music-list'
export default {
	data() {
		return {
			songs: []
		};
	},
	computed: {
		title() {
			return this.singer.name;
		},
		bgImage() {
			return this.singer.avatar;
		},
		...mapGetters([
			'singer'
		])
	},
	watch: {
		/*songs() {
			this._normalizeSongs(this.songs)
		}*/
	},
	created() {
		this.$nextTick(() => {
			//setTimeout(() => {
				this._getSingerTrack();
			//},1000)
			
			
		})
	},
	methods: {
			_getSingerTrack() {
				//console.log(this.singer.id)
				if (!this.singer.id) {
					this.$router.push('/singer');
					return;
				}
				getSingerDetail(this.singer.id).then((res) => {
					if (res.code === ERR_OK) {
						
						this.songs = this._normalizeSongs(res.data.list);
						//console.log(this.songs)
					}
				})
			/*if (!this.singer.id) {
				this.$router.push('/singer');
				return;
			}
			axios.get('http://rapapi.org/mockjsdata/26432/track')
			.then(res => {
				//console.log(res)
				this.songs = this._normalizeSongs(res.data.data.list);
				
			})
			.catch(err => console.log(err))*/
		},
		_normalizeSongs(list) {

			let ret = [];			
			list.forEach((item) => {			
				let {musicData} = item;
				if (musicData.songid && musicData.albummid) {
					ret.push(createSong(musicData))
				}
			})
			return ret;

		}
	},
	components: {
		musiclist
	}
}
</script>

<style lang="stylus">
	@import '~common/stylus/variable'
	
	
</style>


