<template>
	<div class="loading">
		<img src="./loading.gif" width="24" height="24">
		<p class="desc">{{title}}</p>
	</div>
</template>

<script>
export default {
	props: {
		title: {
			type: String,
			default: '正在加载...'
		}
	}
}
</script>

<style scoped lang="stylus">
	@import '~common/stylus/variable'
	.loading
		width 100%
		text-align center
		img
			display inline-block
		.desc
			line-height 20px
			font-size $font-size-small
			color $color-text-l
</style>


